*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}


.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  width: 600px;
  margin: 0 auto;
  
}

h1{
  color: blue;
}
p{
  padding: px;
}
.navbar .links{
  margin-left: auto;
}

.navbar a{
  text-decoration: none;
  padding: 10px;
  margin-left: 16px;
  color: #000;
}
.navbar a:hover{
  color: blue
}
.content{
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}


.blog-preview{
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid gray;
}

.blog-preview h2{
  font-size: 20px;
  color: blue;
  margin-bottom: 8px;
  
}
.blog-preview a{
  text-decoration: none;
  color: black;
}

.create label{
  text-align: left;
  display: block;
}
.create h2{
  font-size: 20px;
  color: blue;
  margin-bottom: 30px;
  text-align: center;
}
.create input, .create textarea, .create select{
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
  box-shadow: green;
}
.create button{
  background: blue;
  color: #fff;
  padding: 8px;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
}
.blog-details button{
  background: blue;
  color: #fff;
  padding: 8px;
  border: 0;
  border-radius: 6px;
  width: 5vw;
  cursor: pointer;
}
.not-found a{

  color: #000;
  
}